html, body {
  height: 100%;
  width: 100%;
  min-width: 360px;
  margin: 0;
  padding: 0;
  background-color: #234;
  color: #ccc;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  overflow-y: scroll;
}

*, *::before, *::after {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App div.page-loading {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
  width: 100%;
  background-color: #fff;
}

.Header {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 60px;
  background-color: #111;
  border-top: 5px solid #678;
  border-bottom: 5px solid #678;
}

.Header img.logo {
  margin: auto 20px;
  max-height: 100%;
  height: 40px;
  width: auto;
}

.Header h1 {
  text-transform: uppercase;
  font-size: 18px;
  margin: 0;
}

.SearchBar {
  background-color: #012;
  border-bottom: 5px solid #678;
}

.SearchBar form {
  width: 100%;
  display: grid;
  grid-template-areas:
    "nameLabel nameInput nameInput nameInput setLabel setInput setInput setInput"
    "rarityLabel rarityInput typeLabel typeInput subtypeLabel subtypeInput supertypeLabel supertypeInput"
    "Submit Submit Submit Submit Submit Submit Submit Submit";
  grid-template-columns: repeat(8, 10%);
  grid-template-rows: auto;
  grid-gap: 5px;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 20px 0;
}

.SearchBar label[for="name"] {
  grid-area: nameLabel;
}

.SearchBar input#name {
  grid-area: nameInput;
}

.SearchBar label[for="setList"] {
  grid-area: setLabel;
}

.SearchBar input#setList {
  grid-area: setInput;
}

.SearchBar label[for="rarity"] {
  grid-area: rarityLabel;
}

.SearchBar select#rarity {
  grid-area: rarityInput;
}

.SearchBar label[for="type"] {
  grid-area: typeLabel;
}

.SearchBar select#type {
  grid-area: typeInput;
}

.SearchBar label[for="subtype"] {
  grid-area: subtypeLabel;
}

.SearchBar select#subtype {
  grid-area: subtypeInput;
}

.SearchBar label[for="subtype"] {
  grid-area: supertypeLabel;
}

.SearchBar select#supertype {
  grid-area: supertypeInput;
}

.SearchBar input[type="submit"] {
  grid-area: Submit;
  text-align: center;
  margin: 20px auto 0px auto;
  width: 80%;
  min-width: 320px;
  height: auto;
  font-size: 14px;
  color: #fff;
  padding: 10px 20px;
  background-color: #222;
  border: 2px solid transparent;
}

.SearchBar input[type=submit]:hover {
  border: 2px solid green;
}

.SearchBar input[type=submit]:focus {
  background-color: #333;
}

.SearchBar input[type=submit]:disabled {
  background-color: #222;
  color: #aaa;
  cursor: not-allowed;
  border: 2px solid transparent;
}

.SearchBar input,
.SearchBar select {
  height: 30px;
  color: grey;
  background-color: #333;
  border: 2px solid transparent;
  margin: auto 2px;
}
.SearchBar input {
  padding: 0 5px;
}
.SearchBar input:focus,
.SearchBar select:focus {
  border: 2px solid green;
}

.SearchBar label {
  text-align: right;
  margin: auto 10px;
}

@media screen and (max-width: 768px) {
  .SearchBar form {
    grid-template-areas:
      "nameLabel nameInput nameInput nameInput"
      "setLabel setInput setInput setInput"
      "rarityLabel rarityInput typeLabel typeInput"
      "subtypeLabel subtypeInput supertypeLabel supertypeInput"
      "Submit Submit Submit Submit";
    grid-template-columns: repeat(4, 20%);
  }
}

@media screen and (max-width: 420px) {
  .SearchBar form {
    grid-template-areas:
      "nameLabel nameInput nameInput"
      "setLabel setInput setInput"
      "rarityLabel rarityInput rarityInput"
      "typeLabel typeInput typeInput"
      "subtypeLabel subtypeInput subtypeInput"
      "supertypeLabel supertypeInput supertypeInput"
      "Submit Submit Submit";
    grid-template-columns: repeat(3, 30%);
  }
}

.Buttons {
  text-align: center;
  padding: 10px 0;
}

.Buttons div.ratio {
  font-size: 24px;
  font-weight: bold;
  text-shadow: 2px 2px 3px #731;
  margin: 10px auto;
}

.Buttons > button {
  display: inline-block;
  width: 150px;
  border: 2px outset #9AC;
  background-color: #35D;
  color: #ccc;
  padding: 6px 12px;
  font-size: 14px;
  cursor: pointer;
  border-radius: 4px;
  margin: auto 10px;
  text-shadow: 0px 1px 1px #000;
}

.Buttons > button:hover {
  color: #fff;
  background-color: #249;
}

.Buttons > button:active {
  color: #fff;
  background-color: #249;
  border-style: inset;
}

.Buttons > button:focus {
  outline: 1px dotted #aaa;
}

.Buttons > button:disabled {
  cursor: not-allowed;
  background-color: #57E;
  color: #aaa;
  border-style: solid;
  border-color: grey;
  text-shadow: none;
}

.CardListContainer {
  color: #456;
  list-style: none;
  padding: 0 0 25px 0 ;
}

.CardListContainer li.not-found {
  text-align: center;
}

.CardListContainer li.not-found img {
  max-width: 300px;
  height: auto;
  margin: auto;
}

.Card {
  color: #FFF;
  width: 20%;
  display: inline-block;
  vertical-align: top;
  text-align: center;
  -webkit-filter: drop-shadow(1px 3px 5px #000);
          filter: drop-shadow(1px 3px 5px #000);
  cursor: pointer;
}

.Card img {
  max-width: 80%;
  margin: 20px auto;
  display: inline-block;
  vertical-align: top;
}

.Card img.imgHiRes {
  max-height: 80%;
  width: auto;
  margin: auto;
}

@media screen and (max-width: 1200px) {
  .Card {
    width: 25%;
  }
}

@media screen and (max-width: 960px) {
  .Card {
    width: calc(100%/3);
  }
}

@media screen and (max-width: 768px) {
  .Card {
    width: calc(50%);
  }
}

@media screen and (max-width: 480px) {
  .Card {
    width: calc(100%);
  }
}

@-webkit-keyframes loading {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  20% {
    -webkit-transform: scale(1, 2.2);
            transform: scale(1, 2.2);
  }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes loading {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  20% {
    -webkit-transform: scale(1, 2.2);
            transform: scale(1, 2.2);
  }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.loading {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 50px 0;
}
.loading-bar {
  display: inline-block;
  width: 8px;
  height: 50px;
  border-radius: 8px;
  margin: auto 5px;
  -webkit-animation: loading 1s ease-in-out infinite;
          animation: loading 1s ease-in-out infinite;
}
.loading-bar:nth-child(1) {
  background-color: #3498db;
  -webkit-animation-delay: 0;
          animation-delay: 0;
}
.loading-bar:nth-child(2) {
  background-color: #c0392b;
  -webkit-animation-delay: 0.09s;
          animation-delay: 0.09s;
}
.loading-bar:nth-child(3) {
  background-color: #f1c40f;
  -webkit-animation-delay: .18s;
          animation-delay: .18s;
}
.loading-bar:nth-child(4) {
  background-color: #27ae60;
  -webkit-animation-delay: .27s;
          animation-delay: .27s;
}

